
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'

import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'



// console.log(vm)

export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      text: "<b>v-text</b>,html",
      checked  : true,
      picked   : 'one',
      selected : 'two',
      multiSelect: ['one', 'three'],
      isClass : false
    }
  },
  filters: {
    json2 :function(value) {
      return JSON.stringify(value).split(',');
    }
  },
  components: {
    Group,
    Cell,
    XHeader,
    ViewBox,
    Tabbar,
    TabbarItem
  },
  methods: {
    
  },
  computed: {
    // a computed getter
    reversedMessage: function () {
      // `this` points to the vm instance
      if(this.picked == 'one'){
        this.isClass = false;
      }else{
        this.isClass = true;
      }
      return this.isClass;
    }
  }
}



